.myFont_01 {
  font-family: 'myFont_01';
}
.ant-layout-header {
  background-color: unset;
  height: 212px;
  padding: 0 15%;
  background: #FFFFFF;
}
.ant-layout-content {
  background: #FFFFFF;
  overflow: hidden;
}
.ant-layout-footer {
  padding: 24px 15%;
  background: #262626;
  font-size: 14px;
}
@media screen and (max-width: 1200px) {
  .ant-layout-content {
    padding: 0;
  }
  .ant-layout-content .hundred-county-container {
    padding: 0 20px;
  }
}
