.myFont_01 {
  font-family: 'myFont_01';
}
.footer-container {
  text-align: center;
  color: #A2A4A5;
}
.footer-container .footer-menu {
  position: relative;
  color: #A2A4A5;
  margin: 20px 0 30px 0;
  padding-bottom: 20px;
}
.footer-container .footer-menu .ant-btn-group .ant-btn {
  color: #A2A4A5;
  border-color: #A2A4A5;
  margin: 10px 15px;
  border-radius: 45px;
}
.footer-container .footer-menu .ant-btn-group .ant-btn:hover {
  color: #1B63DA;
  border-color: #1B63DA;
}
.footer-container .footer-menu::after {
  position: absolute;
  content: '';
  width: 150%;
  height: 1px;
  left: -28.5%;
  bottom: 0;
  background-color: #A2A4A5;
}
