.myFont_01 {
  font-family: 'myFont_01';
}
.pass-platform-container {
  padding: 0 15%;
}
.pass-platform-container .ant-input-search {
  padding: 0 30px;
}
.pass-platform-container .ant-input-search .ant-input-search-button {
  padding: 0 25px;
}
.pass-platform-container .ant-list-items {
  margin-top: 30px;
}
.pass-platform-container .ant-list-split .ant-list-item {
  border: 0;
}
.ant-list-items .ant-list-item:nth-child(1) {
  padding-top: 0;
}
.list-item {
  display: flex;
  padding: 10px 30px;
}
.list-item .img {
  margin-right: 30px;
  flex: 1;
}
.list-item .right {
  padding-right: 20%;
}
.list-item .right .title {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
}
.list-item .right .footer {
  margin-top: 5px;
}
.list-item .right .footer .publish {
  font-size: 15px;
  display: inline-block;
  margin: 8px 20px 0 0;
}
.list-item .right .footer .tag {
  color: #3652cb;
}
